import React, {useCallback, useEffect, useState} from 'react';
import './SubNavFooter.scss'

const SubNavFooter = ({item}) => {

        const [subnav, setSubnav] = useState(false);
        const [dropMenu, setDropMenu] = useState(false);

        const showSubNav = () => setSubnav(!subnav);

        const showMenu = useCallback(() => {
            if (window.innerWidth < 768) {
                setDropMenu(true)
            } else {
                setDropMenu(false)
            }
        }, []);

        useEffect(() => {
            if (window.innerWidth < '768') {
                showMenu()
            }
            window.addEventListener('resize', showMenu)
        }, [showMenu])

        return (
            <>
                {dropMenu ?
                    <>
                        <div className="footer__nav__block">
                            <div onClick={item.subNav && showSubNav}
                                   className={`nav__title__wrapper ${item.className}`}>
                                <p className="nav__title">{item.title}</p>
                                {item.subNav && subnav
                                    ? <i className="fas fa-chevron-down footer"/>
                                    : item.subNav
                                        ? <i className="fas fa-chevron-up"/>
                                        : null}
                            </div>
                            {subnav &&
                            item.subNav.map((item, index) => {
                                return (
                                    <a href={item.path} className='footer__nav__item' key={index}>
                                        <p>{item.title}</p>
                                    </a>
                                );
                            })}
                        </div>
                    </>
                    :
                    <>
                        <div className="footer__nav__block">
                            {/*<div className={`nav__title__wrapper ${item.className}`}>*/}
                                <p className="nav__title_2">{item.title}</p>
                            {/*</div>*/}
                            {item.subNav.map((item, index) => {
                                return (
                                    <a href={item.path} className='footer__nav__item' key={index}>
                                        <p>{item.title}</p>
                                    </a>
                                );
                            })}
                        </div>
                    </>
                }
            </>
        );
    }
;

export default SubNavFooter;